.banner {
    color: black;
    object-fit: contain;
    height: 548px;
  }
  
  .banner-contents {
    margin-left: 30px;
    padding-top: 140px;
    height: 250px;
  }
  
  .banner-title {
    font-size: 3rem;
    font-weight: 700;
    padding-bottom: 1.7rem;
  }
  
  .banner-description {
    width: 80rem;
    line-height: 1.8;
    padding-top: 3.5rem;
    font-size: 0.9rem;
    max-width: 550px;
    height: 100px;
  }
  
  .banner-button {
    cursor: pointer;
    color: #fff;
    outline: none;
    border: none;
    font-weight: 700;
    border-radius: 0.2vw;
    padding-left: 2rem;
    padding-right: 2rem;
    margin-right: 1rem;
    padding-top: 0.5rem;
    background-color: rgba(51, 51, 51, 0.5);
    padding-bottom: 0.5rem;
  }
  
  .banner-button:hover {
    color: #000;
    background-color: #e6e6e6;
    transition: all 0.2s;
  }
  
  .banner-fadeButton {
    height: 7.4rem;
    background-image: linear-gradient(
      180deg,
      transparent,
      rgba(37, 37, 37, 0.61),
      #111
    );
  }